'use strict';

const DeclensionForms = require('./declension-forms');
const FullName = require('./full-name');

class DeclensionResult extends DeclensionForms {

  constructor(props) {
    super(props);

    if (props[''] !== undefined) {
      this.gender = props[''];
      this[''] = this.gender;
    }

    if (props[''] !== undefined) {
      this.plural = new DeclensionForms(props['']);
      this[''] = this['plural'];
    }

    if (props[''] !== undefined) {
      this.where = props[''];
      this.locative = this.where;
      this.gde = this.where;
      this[''] = this.where;
    }

    if (props[''] !== undefined) {
      this.where_to = props[''];
      this.kuda = this.where_to;
      this[''] = this.where_to;
    }

    if (props[''] !== undefined) {
      this.where_from = props[''];
      this.whence = this.where_from;
      this.otkuda = this.where_from;
      this[''] = this.where_from;
    }

    if (props[''] !== undefined) {
      this.fullName = new FullName(props['']);
      this[''] = this.fullName;
    }
  }

}

module.exports = DeclensionResult;